'use client'

import { useStore } from '@/store/useStore'
import { useState } from 'react'
import Image from 'next/image'

const categories = [
    { id: 'COFFEE', name: '커피' },
    { id: 'LATTE', name: '라떼' },
    { id: 'JUICE', name: '주스' },
    { id: 'ADE', name: '에이드' },
    { id: 'TEA', name: '차' },
    { id: 'SMOOTHIE', name: '스무디' },
    { id: 'FRAPE', name: '프라페' },
    { id: 'DESSERT', name: '디저트' },
]

export default function MenuPage() {
    const { menu, addToCart } = useStore()
    const [selectedCategory, setSelectedCategory] = useState('COFFEE')
    const [showNotification, setShowNotification] = useState(false)
    const [notificationMessage, setNotificationMessage] = useState('')

    const filteredMenu = menu.filter((item) => item.category === selectedCategory)

    const handleAddToCart = (item: any) => {
        addToCart(item)
        setNotificationMessage(`${item.name}이(가) 장바구니에 추가되었습니다.`)
        setShowNotification(true)
        setTimeout(() => {
            setShowNotification(false)
        }, 2000)
    }

    return (
        <div className="container mx-auto px-4 py-8">
            {/* 알림 메시지 */}
            {showNotification && (
                <div className="fixed top-4 right-4 bg-green-500 text-white px-4 py-2 rounded shadow-lg z-50">
                    {notificationMessage}
                </div>
            )}

            {/* 카테고리 탭 */}
            <div className="flex space-x-2 mb-6 overflow-x-auto pb-2">
                {categories.map((category) => (
                    <button
                        key={category.id}
                        onClick={() => setSelectedCategory(category.id)}
                        className={`px-4 py-2 rounded-full whitespace-nowrap ${selectedCategory === category.id
                                ? 'bg-blue-500 text-white'
                                : 'bg-gray-200 text-gray-700'
                            }`}
                    >
                        {category.name}
                    </button>
                ))}
            </div>

            {/* 메뉴 그리드 */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredMenu.map((item) => (
                    <div
                        key={item.id}
                        className="bg-white rounded-lg shadow-md overflow-hidden"
                    >
                        <div className="relative h-48">
                            <Image
                                src={item.image}
                                alt={item.name}
                                fill
                                className="object-cover"
                            />
                        </div>
                        <div className="p-4">
                            <h3 className="text-lg font-semibold">{item.name}</h3>
                            <p className="text-gray-600">
                                {item.price.toLocaleString()}원
                            </p>
                            <button
                                onClick={() => handleAddToCart(item)}
                                className="mt-2 w-full bg-blue-500 text-white py-2 rounded hover:bg-blue-600 transition-colors"
                            >
                                장바구니에 담기
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
} 